<template>
  <div class="home">
    <el-button type="primary" @click="codeErr">js</el-button>
    <el-button type="success" @click="asyncError"></el-button>
    <el-button type="danger" @click="promiseErr">promise</el-button>
    <el-button type="info" @click="xhrError">xhr</el-button>
    <el-button type="warning" @click="fetchError">fetch</el-button>
    <el-button type="danger" @click="resourceError"></el-button>
    <p class="error"></p>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="message" label="" width="300"> </el-table-column>
      <el-table-column prop="pageUrl" label=""> </el-table-column>
      <el-table-column prop="time" label="" width="150">
        <template slot-scope="scope">
          <span>{{ scope.row.time ? format(scope.row.time) : scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="apikey" label=""> </el-table-column>
      <el-table-column prop="userId" label="id"> </el-table-column>
      <el-table-column prop="sdkVersion" label="SDK"> </el-table-column>
      <el-table-column prop="deviceInfo" label="">
        <template slot-scope="scope">
          <span>{{ scope.row.deviceInfo.browser }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="deviceInfo" label="">
        <template slot-scope="scope">
          <span>{{ scope.row.deviceInfo.os }}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" prop="recordScreenId" label="" width="100">
        <template slot-scope="scope">
          <el-button v-if="scope.row.type == 'error' || scope.row.type == 'unhandledrejection'" type="primary" @click="revertCode(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column fixed="right" prop="recordScreenId" label="" width="100">
        <template slot-scope="scope">
          <el-button v-if="scope.row.recordScreenId" type="primary" @click="playRecord(scope.row.recordScreenId)"></el-button>
        </template>
      </el-table-column>
      <el-table-column fixed="right" prop="breadcrumb" label="" width="125">
        <template slot-scope="scope">
          <el-button v-if="scope.row.breadcrumb" type="primary" @click="revertBehavior(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :title="dialogTitle" :class="{ 'revert-disalog': fullscreen }" top="10vh" :fullscreen="fullscreen" :visible.sync="revertdialog" width="90%" :destroy-on-close="true">
      <div id="revert" ref="revert" v-if="dialogTitle != ''"></div>
      <el-timeline v-else>
        <el-timeline-item v-for="(activity, index) in activities" :key="index" :icon="activity.icon" :color="activity.color" :timestamp="format(activity.time)">
          {{ activity.content }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
    <div id="echart" style="width: 400px; height: 300px"></div>
  </div>
</template>

<script>
import { findCodeBySourceMap } from '../utils/sourcemap';
import { unzip } from '../utils/recordScreen.js';
import rrwebPlayer from 'rrweb-player';
import 'rrweb-player/dist/style.css';

export default {
  name: 'HomeView',
  data() {
    return {
      fullscreen: true,
      revertdialog: false,
      dialogTitle: '',
      activities: [],
      tableData: []
    };
  },
  created() {
    this.getTableData();
  },
  mounted() {
    this.myEcharts();
  },
  methods: {
    myEcharts() {
      // domecharts
      let myChart = this.$echarts.init(document.getElementById('echart'));

      // 
      let option = {
        animation: false,
        title: {
          text: 'ECharts '
        },
        tooltip: {},
        legend: {
          data: ['']
        },
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }
        ]
      };

      // 
      myChart.setOption(option);
    },
    getTableData() {
      setTimeout(() => {
        fetch(`http://localhost:8083/getErrorList`)
          .then((response) => response.json())
          .then((res) => {
            this.tableData = res.data;
          });
      }, 500);
    },
    fetchError() {
      fetch('https://jsonplaceholder.typicode.com/posts/a')
        .then((res) => {
          if (res.status == 404) {
            this.getTableData();
          }
        })
        .catch(() => {
          this.getTableData();
        });
    },
    revertBehavior({ breadcrumb }) {
      this.dialogTitle = '';
      this.fullscreen = false;
      this.revertdialog = true;
      breadcrumb.forEach((item) => {
        item.color = item.status == 'ok' ? '#5FF713' : '#F70B0B';
        item.icon = item.status == 'ok' ? 'el-icon-check' : 'el-icon-close';
        if (item.category == 'Click') {
          item.content = `dom: ${item.data}`;
        } else if (item.category == 'Http') {
          item.content = `: ${item.data.url}, ${item.status == 'ok' ? '' : ''}`;
        } else if (item.category == 'Code_Error') {
          item.content = `${item.data.message}`;
        } else if (item.category == 'Resource_Error') {
          item.content = `${item.message}`;
        } else if (item.category == 'Route') {
          item.content = ` ${item.data.from}  ${item.data.to}`;
        }
      });
      this.activities = breadcrumb;
    },
    revertCode(row) {
      findCodeBySourceMap(row, (res) => {
        this.dialogTitle = '';
        this.fullscreen = false;
        this.revertdialog = true;
        this.$nextTick(() => {
          this.$refs.revert.innerHTML = res;
        });
      });
    },
    playRecord(id) {
      fetch(`http://localhost:8083/getRecordScreenId?id=${id}`)
        .then((response) => response.json())
        .then((res) => {
          let { code, data } = res;
          if (code == 200 && Array.isArray(data) && data[0] && data[0].events) {
            let events = unzip(data[0].events);
            this.fullscreen = true;
            this.dialogTitle = '';
            this.revertdialog = true;
            this.$nextTick(() => {
              new rrwebPlayer({
                target: document.getElementById('revert'),
                props: {
                  events,
                  UNSAFE_replayCanvas: true
                }
              });
            });
          } else {
            this.$message({
              message: '~',
              type: 'warning',
              duration: 5000
            });
          }
        });
    },
    format(time) {
      let str = new Date(time);
      return str.toLocaleDateString().replace(/\//g, '-') + ' ' + str.toTimeString().substr(0, 8);
    },
    asyncError() {
      this.getTableData();
      setTimeout(() => {
        JSON.parse('');
      });
    },
    codeErr() {
      this.getTableData();
      let a = undefined;
      if (a.length) {
        console.log('1');
      }
    },
    resourceError() {
      let script = document.createElement('script');
      script.type = 'text/javascript';
      script.src = 'https://abc.com/index.js';
      document.body.appendChild(script);
      // 
      script.onerror = () => {
        this.getTableData();
      };
    },
    promiseErr() {
      new Promise((resolve) => {
        this.getTableData();
        let person = {};
        person.name.age();
        resolve();
      });
    },

    xhrError() {
      let _this = this;
      let ajax = new XMLHttpRequest();
      ajax.open('GET', 'https://abc.com/test/api');
      ajax.setRequestHeader('content-type', 'application/json');
      ajax.onreadystatechange = function () {
        if (ajax.readyState == 4) {
          _this.getTableData();
        }
        if (ajax.status === 200 || ajax.status === 304) {
          console.log('ajax', ajax);
        }
      };
      ajax.send();
      ajax.addEventListener('loadend', () => {});
    }
  }
};
</script>
<style lang="less">
.error {
  margin-top: 20px;
  height: 30px;
  line-height: 30px;
  font-weight: 800;
  background-color: #ebeef5;
}
.el-row {
  text-align: left;
  margin-bottom: 10px;
}
.el-dialog__header {
  font-size: 20px;
  font-weight: 800;
}
.el-timeline {
  text-align: left;
  .el-timeline-item__icon {
    font-size: 12px;
  }
}
.revert-disalog {
  .el-dialog__body {
    height: 720px;
  }
}
.rr-player {
  margin: 0 auto;
}
#revert {
  width: 100%;
  display: flex;
}
.errdetail {
  text-align: left;
  width: 100%;
  font-size: 16px;
}
.code-line {
  padding: 5px 0;
}
.heightlight {
  background-color: yellowgreen;
}
.errheader {
  padding: 10px;
  border-bottom: 1px solid rgb(214, 210, 210);
}
</style>
